<template>
  <div class="main">
    <header>
      <!-- <h4>项目管理 - 项目列表 - 股权信息</h4> -->
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path:'/manage/projectlist'}">项目管理</el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path:'/manage/projectlist'}">项目列表</el-breadcrumb-item>
        <el-breadcrumb-item>股权信息</el-breadcrumb-item>
      </el-breadcrumb>      
    </header>
    <nav>
      项目名称：<span>亚洲首座智能充电站</span>
    </nav>
    <!-- 股权信息&&变更信息 -->
    <el-tabs type="border-card">
      <el-tab-pane label="股权信息">
        <!-- 主要内容 -->
        <section>
          <el-table :data="tableData" style="width: 100%;padding-left: 100px">
            <el-table-column prop="name" label="股东" width="180">
            </el-table-column>        
            <el-table-column prop="phone" label="电话" width="180">
            </el-table-column>
            <el-table-column prop="equity" label="股权" width="180">
            </el-table-column>
          </el-table>
        </section>
        <footer>
          <el-upload class="upload-demo" action="https://jsonplaceholder.typicode.com/posts/" :on-preview="handlePreview" :on-remove="handleRemove" :before-remove="beforeRemove" multiple :file-list="fileList" accept=".xlsx" :limit="1" :on-exceed="handleExceed">
            <el-button plain icon="el-icon-upload2" size="small" type="primary">点击上传</el-button>
            <div slot="tip" class="el-upload__tip">支持扩展名：.xlsx文件</div>
            <div slot="tip" class="el-upload__tip">注：该处的电话非必填，但是只有注册用户和电话匹配后既视为有投资项目</div>
          </el-upload>
        </footer>        
      </el-tab-pane>
      <el-tab-pane label="变更信息">
        <!-- 主要内容 -->
        <section>
          <el-table :data="tableData2" style="width: 100%;padding-left: 100px">
            <el-table-column prop="transferor" label="出让人" width="180">
            </el-table-column>        
            <el-table-column prop="assignee" label="受让人" width="180">
            </el-table-column>
            <el-table-column prop="time" label="时间" width="180">
            </el-table-column>
          </el-table>
        </section>
        <footer class="myForm">
          <el-form ref="form" :model="form" label-width="80px">
            <el-form-item>
              <el-input v-model="form.transferor" placeholder="输入出让人信息"></el-input>
            </el-form-item>
            <el-form-item>
              <el-input v-model="form.assignee" placeholder="输入受让人信息"></el-input>
            </el-form-item> 
            <el-form-item>
              <el-date-picker type="date" placeholder="输入时间" v-model="form.time"></el-date-picker>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="onSubmit">新增</el-button>
            </el-form-item>
          </el-form>
        </footer>     
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        tableData: [{
          name: '王小虎',
          phone: '13632756287',
          equity: '11%'
        }, {
          name: '王小虎',
          phone: '13632756287',
          equity: '12%'
        }, {
          name: '王小虎',
          phone: '13632756287',
          equity: '9%'
        }, {
          name: '王小虎',
          phone: '13632756287',
          equity: '10%'
        }],
        tableData2: [{
          transferor: '小夫',
          assignee: '胖虎',
          time: '2019-09-17'
        }, {
          transferor: '小夫',
          assignee: '胖虎',
          time: '2019-09-17'
        }, {
          transferor: '小夫',
          assignee: '胖虎',
          time: '2019-09-17'
        }, {
          transferor: '小夫',
          assignee: '胖虎',
          time: '2019-09-17'
        }],        
        fileList: [],
        form: {
          transferor: '',
          assignee: '',
          time: ''
        }
      }
    },
    methods:{
      handleRemove(file, fileList) {
        console.log(file, fileList);
      },
      handlePreview(file) {
        console.log(file);
      },
      handleExceed(files, fileList) {
        this.$message.warning(`当前限制选择 1 个文件，本次选择了 ${files.length} 个文件，共选择了 ${files.length + fileList.length} 个文件`);
      },
      beforeRemove(file, fileList) {
        return this.$confirm(`确定移除 ${ file.name }？`);
      },
      onSubmit() {
        console.log('submit!');
        // <!-- 消息提示 -->
        if(true){
          this.$message({
            message: '添加成功',
            type: 'success'
          });
          // 转换时间格式
          var date=new Date(this.form.time);
          var y=date.getFullYear();
          var m=date.getMonth()+1;
          m<10&&(m="0"+m);
          var d=date.getDate();
          d<10&&(d="0"+d);
          this.form.time = `${y}-${m}-${d}`;
          // 插入tableData2表，这样就无需再次请求ajax
          this.tableData2.push({transferor:this.form.transferor,assignee:this.form.assignee,time:this.form.time});
        }else{
          this.$message.error('添加失败，变更信息必须已在企业列表内');
        }
      }
    }
  }
</script>

<style scoped>
/* 标题部分 */
.main,header{
  width:100%;
  margin-top:-2px;
}

/* 项目名称 */
.main>nav:nth-child(2){
  font-weight: bold;
  padding:10px 20px 20px 20px;
  background-color:#fff;
}

.main>nav:nth-child(2)>span{
  color:#5167B7;
}

/* 上传文件 */
.main>footer{
  margin:50px 0 0 20px;
}

/* 变更信息的输入表格部分 */
.myForm>.el-form>.el-form-item{
  display: inline-block;
}

.myForm>.el-form>.el-form-item:nth-child(2)>.el-form-item__content{
  margin-left: 10px!important;
}

</style>



