<template>
  <div>
    <!-- 顶部 -->
    <header class="my_header">
      <!-- 顶部左侧位置 -->
      <div class="left-title">
        <span>聚众力后台管理系统</span>
      </div>
      <!-- 顶部右侧位置 -->
      <div class="right-title">
        <span class="icon-sign icon-human" @click="jump">西蒙船长</span>          
        <span class="icon-sign icon-exit" @click="exit()" title="o(╥﹏╥)o">退出</span>
      </div>
    </header>
    <!-- 主体 -->
    <section class="myBody">
      <!-- 左边导航栏部分 -->
      <el-col :span="3" style="width:237.88px;">
        <article class="nav">
          <ul>
            <li class="father">
              <span class="icon-sign icon-human">用户管理</span>
            </li>
            <li class="son" ref="box1" @click="navbar('box1')">
              <router-link to="/manage">用户列表</router-link>
            </li>
            <li class="father">
              <span class="icon-sign icon-company">企业管理</span>
            </li>
            <li class="son" ref="box3" @click="navbar('box3')">
              <router-link to="/manage/companylist">企业列表</router-link>
            </li>                 
            <li class="father">
              <span class="icon-sign icon-project">项目管理</span>
            </li>
            <li class="son" ref="box2" @click="navbar('box2')">
              <router-link to="/manage/projectlist">项目列表</router-link>
            </li>       
            <li class="father" v-show="superman">
              <span class="icon-sign icon-system">系统设置</span>
            </li>
            <li class="son" ref="box4" @click="navbar('box4')" v-show="superman">
              <router-link to="/manage/setjurisdiction">权限管理</router-link>
            </li>
            <li class="son" ref="box5" @click="navbar('box5')" v-show="superman">
              <router-link to="/manage/setadministrator">管理员管理</router-link>
            </li>
            <li class="son" ref="box6" @click="navbar('box6')" v-show="superman">
              <router-link to="/manage/setlog">系统日志</router-link>
            </li>
          </ul>
        </article>
      </el-col>
      <!-- 右边主体部分 -->
      <el-col :span="21" style="background:#F0F0F2">
        <!-- <transition enter-active-class="animated zoomInDown delay-1s" leave-active-class="animated zoomOutRight"> -->
          <router-view></router-view>
        <!-- </transition> -->
      </el-col>
    </section>
    <!-- 无聊做一个电梯效果来玩 -->
    <nav class="elevator" id="toTop">
      返回<br>顶部
    </nav>
  </div>
</template>

<script>

export default {
  data(){
    return {
      boxIndex :'',
      userId:'CS2020',
      superman:true
    }
  },
  mounted(){
    if(this.boxIndex==''){
      this.$refs.box1.style.background="#65B312";
      this.$refs.box1.querySelector("a").style.color="#fff";
      this.$router.push("/manage");
    }
    // console.log(this.$refs.box1);
    // console.log(this.boxIndex);
    // 电梯
    window.onscroll=function(){
    let scrollTop=document.body.scrollTop||document.documentElement.scrollTop;
    let toTop = document.getElementById("toTop");
    if(scrollTop>=500)
      toTop.style.display="block";
    else
      toTop.style.display="none";
    }
    //手动控制滚动
    toTop.onclick=function(){
      window.scrollTo(0,0);//(横向滚动位置，纵向滚动位置)
    }    
  },
  methods:{
    // 定义这个方法是为了让点击左侧导航栏可以有颜色的改变
    navbar(index){
      // console.log(this.$refs[index]);
      // console.log(index);
      this.boxIndex = index;
      var elems=document.querySelectorAll("section.myBody>div.el-col>article.nav>ul>li.son");
      for(var i=0;i<elems.length;i++){
        elems[i].style.background="#083B58";
        elems[i].querySelector("a").style.color="#657F8B";
      }
      this.$refs[index].style.background="#65B312";
      this.$refs[index].querySelector("a").style.color="#fff";     
    },
    //退出
    exit(){
      this.$router.push("/register");
    },
    // 点击头像跳转
    jump(){
      this.$router.push("/manage/setadministratoradd/"+this.userId);
      var elems=document.querySelectorAll("section.myBody>div.el-col>article.nav>ul>li.son");
      for(var i=0;i<elems.length;i++){
        elems[i].style.background="#083B58";
        elems[i].querySelector("a").style.color="#657F8B";
      }      
      this.$refs.box5.style.background="#65B312";
      this.$refs.box5.querySelector("a").style.color="#fff";
    }
  }

}
</script>

<style scoped>
.my_header{
  background:#024F79;
  height:80px;
  line-height:80px;
  border: 0;
}

.my_header>div{
  display: inline-block;
  line-height:80px;
}

.left-title{
  background-image: url("../img/logo2.jpg");
  width:50%;
  height:80px;
  background-repeat: no-repeat;
  background-position: 30px 50%;
}

.left-title>span{
  color:#fff;
  margin-left:200px;
}

.left-title>span:first-child{
  margin-left:220px;
  font-size:20px;
  font-weight: bold;
  padding:15px 25px;
  border-left:1px solid #fff;
}

.right-title{
  text-align: right;
  width: 47%;
  color:#8F9EA8;
}

.right-title>span{
  font-size:18px;
}

.right-title>span:hover{
  color:#fff;
  font-size:23px;
  cursor: pointer;
}

.right-title>span:last-child:hover{
  background-image: url("../img/icon_10_bai.png");
}

.right-title>span:first-child:hover{
  background-image: url("../img/icon_1_bai.png");
}

/* 设置icon图标的样式 */
.icon-sign{
  width:50px;
  height:50px;
  background-position: 50px 50%;
  background-repeat: no-repeat;
  background-position-x: 0px;
  padding:10px 50px;
}

.icon-human{
  background-image: url("../img/icon_1_hui.png");
}

.icon-exit{
  background-image: url("../img/icon_10_hui.png");
}

.icon-project{
  background-image: url("../img/icon_3_hui.png");
}

.icon-company{
  background-image: url("../img/icon_5_hui.png");
}

.icon-system{
  background-image: url("../img/icon_7_hui.png");
}

/* .nav{
  border:1px solid #BBBBBB;
} */
.nav ul li a,.father{
  color:#657F8B;
}
.nav ul li>a{
  display:inline-block;
  width:100%;
  height:100%;
  padding:15px 0;  
}
.nav ul li.father{
  padding:15px 0;
}
.son{
  text-align: center;
}
.father i{
  margin-left:30px;
}
.father span{
  margin:0 12px;
}
.father{
  font-weight:bold;
  text-align: center;
}

li.son>a:focus,li.son>a:hover,li.son:hover{
  color:#fff!important;
  background-color:#65B312;
}

/* li.son:active,li.son:hover{
  background-color:#CCBBBB;
} */

/* 左边导航栏部分 */
.myBody{
  height:888px;;
}

.myBody>div:first-child{
  height:100%;
  background-color:#083B58;
}

.nav>ul{
  box-shadow: -2px 2px 2px #072B41;
}

.nav>ul>li{
  border-top:2px solid #094A6A;
  border-bottom:1px solid #072B41;
}

.nav>ul>li:first-child{
  border-top:0;
}

.nav{
  overflow-x:hidden;
}

.nav>ul>.son:nth-child(9){
  /* padding-left:85px; */
  width: 253.88px;
}

  /* 电梯按钮 */
  #toTop{
    display: inline-block;
    background-color:rgba(0, 0, 0, .2);
    position: fixed;
    bottom:150px;
    right:100px;
    padding:15px 20px;
    border-radius: 50%;
    color:#fff;
    display: none;
    cursor:pointer;
  }

  #toTop:hover{
    background-color:rgba(0, 0, 0, .5);
    font-weight: bold;  
  }
</style>



