<template>
  <div class="register">
    <!-- 头部 -->
    <!-- <Header></Header> -->
    <!-- 主体 -->
    <section>
      <el-row>
        <!-- 左边logo -->
        <el-col :span="12">
            <div class="grid-content logo">
              <!-- <img src="../../img/logo.jpg" alt=""> -->
            </div>
        </el-col>
        <!-- 右边登录框 -->
        <el-col :span="12">
            <div class="grid-content right">
              <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm" label-position="left" status-icon>
                <h1>聚众力后台管理系统</h1>
                <el-form-item prop="name">
                  <el-input v-model="ruleForm.name" placeholder="请输入用户名" autocomplete="on" autofocus></el-input>
                </el-form-item>
                <el-form-item prop="pass">
                  <el-input type="password" v-model="ruleForm.pass" autocomplete="off" placeholder="请输入用户密码" @keyup.13.native="submitForm('ruleForm')"></el-input>
                </el-form-item>
                <!-- <img src="../../img/login.jpg" alt=""> -->
                <el-button @click="submitForm('ruleForm')"></el-button>
              </el-form>
              <!-- <button @keyup.13.enter.native="search()">点我</button> -->
              <!-- <el-input type="button" @keyup.13.native="search"></el-input>  -->
            </div>
        </el-col>
      </el-row>
    </section>
  </div>
</template>

<script>
// import Header from '@/page/header.vue'

export default {
  name:"register",
  components:{
    // Header
  },
  data() {
    return {
      ruleForm: {
        name: '',
        pass: ''
      },
      rules: {
        name: [
          { required: true, message: '请输入用户名称', trigger: 'blur' },
          { min: 3, max: 10, message: '长度在 3 到 10 个字符', trigger: 'blur' },
          { pattern: /^[A-Za-z0-9\u4e00-\u9fa5]+$/, message: '不允许输入空格等特殊符号' }
        ],
        pass: [
          { required: true, message: '请输入用户密码', trigger: 'blur' },
          { min: 8, max: 12, message: '长度在 8 到 12 个字符', trigger: 'blur' },
          { pattern: /^[A-Za-z0-9\u4e00-\u9fa5]+$/, message: '不允许输入空格等特殊符号' }
        ]
      }
    };
  },
  created(){
    // this.submitForm();
  },
  methods: {
    submitForm(formName) {
      var url = "";
      var uname = this.ruleForm.name;
      var upwd = this.ruleForm.pass;
      this.$refs[formName].validate((valid) => {
        if (valid) {
          console.log(`${uname} ${upwd}`);
          // this.$http.post(url,{
          //   uname,upwd
          // }).then(result=>{
          // if(result.body.code==1){
          // this.$router.push("/manage/");
          // }
          // })
          this.$router.push("/manage/");
        } else {
          this.$message.error('输入有误，请检查用户名或密码!!');
          return false;
        }
      });
    }
  }
}
</script>

<style scoped>
.el-row,.el-col,.right{
  height:100%;
}
  
.row-bg{
    background-color:#f9f9f9;
    padding:10px 0;
}
/* .grid-content{
    height:650px;
} */
.logo{
  display:flex;   /*设置父元素是弹性布局*/
  justify-content:center;/*水平居中*/
  align-items:center;/*垂直对齐方式*/
}
/* 右边登录框 */
.el-form{
  /* border:1px solid #666666; */
  width:285px;
  text-align: center;
  margin:200px 30px 0;
  /* box-shadow:5px 5px #ddd; */
}
.right{
  display:flex;   /*设置父元素是弹性布局*/
  justify-content:left;/*水平居中*/
  align-items:center;/*垂直对齐方式*/
}
/* .el-form-item{
  width:80%;
  margin-left:45px;
} */

h1{
  margin:20px auto;
  color:#05507A;
  font-weight:normal;
}
.el-button{
  margin-bottom:20px;
  width:211px;
  height:50px;
  background-image:url("../../img/login.jpg");
}
.el-button:hover{
  box-shadow:5px 5px #ddd;
}

.register{
  height:970px;
  background-color:#024F79;
}

.register>section{
  background-image:url("../../img/logo.jpg");
  background-repeat: no-repeat;
  background-position: 50% 50%;
  height:100%;
}
</style>


