<template>
  <div class="main">
    <header>
      <!-- <h4>系统设置 - 权限管理</h4> -->
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path:'/manage/'}">用户管理</el-breadcrumb-item>                
        <el-breadcrumb-item :to="{ path:'/manage/'}">用户列表</el-breadcrumb-item>
        <el-breadcrumb-item>所属公司</el-breadcrumb-item>
      </el-breadcrumb>   
    </header>
    <nav>
      用户：<span>大熊</span>
    </nav>
    <section style="background-color:#fff">
      <p>所属公司：（最多绑定100个）</p>
      <section>
        <el-checkbox :indeterminate="isIndeterminate" v-model="checkAll" @change="handleCheckAllChange">全选</el-checkbox>
        <div style="margin: 10px 0;"></div>
        <el-checkbox-group v-model="checkedCities" @change="handleCheckedCitiesChange">
          <el-checkbox v-for="city in cities" :label="city" :key="city">{{city}}</el-checkbox>
        </el-checkbox-group>        
      </section>
    </section>
    <footer style="padding-top:30px;background:#FFFFFF">
      <div class="left-block">
        <el-button plain type="danger" @click="dialogDelete()">
          解绑
        </el-button>
        <el-button plain type="success" @click="dialogFormVisible = true">
          新增
        </el-button>
      </div>
      <!-- 对话框 -->
      <el-dialog title="删除" :visible.sync="dialogVisible" width="30%" :before-close="handleClose" style="text-align:center">
        <span>
          是否确认删除：{{" " + checkedCities}}
        </span>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false">取 消</el-button>
          <el-button type="primary" @click='deleteSelect(cities)'>确 定</el-button>
        </span>
      </el-dialog>
      <el-dialog title="新增" :visible.sync="dialogVisible" width="30%" :before-close="handleClose" style="text-align:center">
        <span>
          是否确认删除：{{" " + checkedCities}}
        </span>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false">取 消</el-button>
          <el-button type="primary" @click='deleteSelect(cities)'>确 定</el-button>
        </span>
      </el-dialog>
      <el-dialog title="绑定企业" :visible.sync="dialogFormVisible" class="elDialog">
        选择企业*
        <el-select v-model="value" filterable placeholder="请选择">
          <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
          </el-option>
        </el-select>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisible = false">取 消</el-button>
          <el-button type="primary" @click="dialogFormVisible = false">确 定</el-button>
        </div>
      </el-dialog>      
      <div class="right-block">
        <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage" :page-sizes="[100, 200, 300, 400]" :page-size="100" layout="total, sizes, prev, pager, next, jumper" :total="400">
        </el-pagination>
      </div>
    </footer>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        tableData: [{
          people: '路飞',
          list: ["客户信息","员工信息","所有订单信息"]
        }, {
          people: '索隆',
          list: ["客户信息","员工信息","所有订单信息"]
        }, {
          people: '香吉士',
          list: ["客户信息","员工信息","所有订单信息"]
        }],
        checkAll: false,
        checkedCities: ['上海', '北京'],
        cities: ['上海', '北京', '广州', '深圳', '日本', '加拿大', '俄罗斯'],
        isIndeterminate: true,//indeterminate 属性用以表示 checkbox 的不确定状态，一般用于实现全选的效果
        currentPage: 1,
        dialogVisible: false,
        dialogFormVisible: false,
        formLabelWidth: '120px',
        options: [{
          value: 'qq',
          label: '腾讯'
        }, {
          value: 'al',
          label: '阿里巴巴'
        }, {
          value: 'hw',
          label: '华为'
        }, {
          value: 'bd',
          label: '百度'
        }, {
          value: 'pg',
          label: 'Phone'
        }],
        value: ''
      }
    },
    methods:{
      handleEdit(index, rows){
        // console.log(rows[index].people);
        this.$router.push("/manage/setamend/"+rows[index].people);
      },
      handleCheckAllChange(val) {
        this.checkedCities = val ? this.cities : [];
        this.isIndeterminate = false;
      },
      handleCheckedCitiesChange(value) {
        let checkedCount = value.length;
        this.checkAll = checkedCount === this.cities.length;
        this.isIndeterminate = checkedCount > 0 && checkedCount < this.cities.length;
        // console.log(this.isIndeterminate);//当全部选中或一个都没选中时，为false
        // console.log(value);
      },
      handleSizeChange(val) {
        console.log(`每页 ${val} 条`);
      },      
      handleCurrentChange(val) {
        console.log(`当前页: ${val}`);
      },
      dialogDelete(){
        if(this.checkedCities.length >= 1){
          this.dialogVisible = true;
        }else{
          this.$message({
          message: '抱歉，您没有选中要解绑的公司',
          type: 'warning'
        });
        }
      },
      deleteSelect(cities){
        if(cities.sort().toString() == this.checkedCities.sort().toString()){
          cities.splice(0, cities.length);
        }else{
          for(var j=0;j<cities.length;j++){
            for( var i=0;i<this.checkedCities.length;i++){
              if(cities[j] == this.checkedCities[i]){
                cities.splice(j, 1);
              }
            }            
          }
        };
        this.$message({
          type: 'success',
          message: '解绑成功: '
        });
        this.dialogVisible = false;
      },
      handleClose(done) {//这一块逻辑是点击对话框之外的区域进行提示是否关闭？
        this.$confirm('确认关闭对话框？')
        .then(_ => {
          done();
        })
        .catch(_ => {});
      }
    }
  }
</script>

<style scoped>
  /* 标题部分 */
  .main,header{
    width:100%;
    margin-top:-2px;
  }

  /* 项目名称 */
  .el-checkbox-group{
    display: inline-block;
    width: 600px;
  }

  .el-checkbox-group>.el-checkbox{
    width:33%;
    margin:10px 0;
  }

  /* 栅格布局 */
 footer{
    padding-bottom:20px;
    padding-right:50px;
    display:flex;   /*设置父元素是弹性布局*/
    justify-content:space-between;
    align-items:center;
  }

  .right-block,.left-block{
    display: inline-block;
  }

  .left-block{
    margin-left:35px;
  }

  .left-block>.el-button:nth-child(2){
    margin-left: 50px;
  }

  .main>section{
    padding-left: 50px;
  }

  .main>section>p{
    font-size:16px;
  }

  .main>section>section{
    margin:20px 0 0 20px;
  }

  .elDialog>.el-dialog>.el-dialog__body>.el-select{
    text-align: center;
    width: 230px;
    margin:10px;
  }
</style>


