import Vue from 'vue'
import Router from 'vue-router'
import RegisterContainer from '@/components/Register/Register.vue'
import Manage from '@/page/manage.vue'
import UserList from '@/components/User/UserList.vue'
import UserCompany from '@/components/User/UserCompany.vue'
import UserParticipation from '@/components/User/UserParticipation.vue'
import UserProject from '@/components/User/UserProject.vue'
import ProjectList from '@/components/Project/ProjectList.vue'
// import ProjectManage from '@/components/Project/ProjectManage.vue'
import ProjectDetails from '@/components/Project/ProjectDetails.vue'
// import ProjectSuggest from '@/components/Project/ProjectSuggest.vue'
// import ProjectPersonnel from '@/components/Project/ProjectPersonnel.vue'
// import ProjectGroup from '@/components/Project/ProjectGroup.vue'
// import ProjectAgreement from '@/components/Project/ProjectAgreement.vue'
import ProjectEquity from '@/components/Project/ProjectEquity.vue'
import ProjectNotice from '@/components/Project/ProjectNotice.vue'
import ProjectEaring from '@/components/Project/ProjectEaring.vue'
// import Management from '@/components/Indent/Management.vue'
// import OrderDetails from '@/components/Indent/Details.vue'
// import RefundOrder from '@/components/Indent/RefundOrder.vue'
// import FinanceDetails from '@/components/Finance/FinanceDetails.vue'
// import FinanceWithdraw from '@/components/Finance/FinanceWithdraw.vue'
// import PaymentHistory from '@/components/Finance/PaymentHistory.vue'
import CompanyList from '@/components/Company/CompanyList.vue'
import ComponentParticipation from '@/components/Company/ComponentParticipation'
import ComponentProject from '@/components/Company/ComponentProject'
import SetJurisdiction from '@/components/Set/SetJurisdiction.vue'
import SetAmend from '@/components/Set/SetAmend.vue'
import SetAdministrator from '@/components/Set/SetAdministrator.vue'
import SetAdministratorAdd from '@/components/Set/SetAdministratorAdd.vue'
import SetLog from '@/components/Set/SetLog'
import Error from '@/components/Error/Error.vue'

Vue.use(Router)

export default new Router({
  // mode: 'history',//去除路由器默认携带的#
  routes: [
    {
      path: '/',
      redirect:"/register"//重定向
    },
    {
      path: '/register',
      name: 'register',
      component:RegisterContainer
    },
    {
      path:'/manage',
      name:'manage',
      component:Manage,
      children:[
        { path: "",name: 'userlist',component: UserList },
        { path: "usercompany",name: 'usercompany',component: UserCompany },
        { path: "userparticipation",name: 'userparticipation',component: UserParticipation },
        { path: "userproject",name: 'userproject',component: UserProject },
        { path:"projectlist",name:'projectlist',component:ProjectList },
        // { path:"projectmanage",name:'projectmanage',component:ProjectManage },
        { path:"projectdetails",name:'projectdetails',component:ProjectDetails },
        { path:"projectdetails/:name",name:'projectdetails',component:ProjectDetails, props:true },
        // { path:"projectsuggest",name:'projectsuggest',component:ProjectSuggest },
        // { path:"projectpersonnel",name:'projectpersonnel',component:ProjectPersonnel },
        // { path:"projectgroup",name:'projectgroup',component:ProjectGroup },
        // { path:"projectagreement",name:'projectagreement',component:ProjectAgreement },
        { path:"projectequity",name:'projectequity',component:ProjectEquity },
        { path:"projectnotice",name:'projectnotice',component:ProjectNotice },
        { path:"projectearing",name:'projectearing',component:ProjectEaring },
        // { path:"management",name:'management',component:Management },
        // { path:"orderdetails",name:'orderdetails',component:OrderDetails },
        // { path:"refundorder",name:'refundorder',component:RefundOrder },
        // { path:"financedetails",name:'financedetails',component:FinanceDetails },
        // { path:"financewithdraw",name:'financewithdraw',component:FinanceWithdraw },
        // { path:"paymenthistory",name:'paymenthistory',component:PaymentHistory },
        { path:"companylist",name:'companylist',component:CompanyList },
        { path:"componentparticipation",name:'componentparticipation',component:ComponentParticipation },
        { path:"componentproject",name:'componentproject',component:ComponentProject },
        { path:"setjurisdiction",name:'setjurisdiction',component:SetJurisdiction },
        { path:"setamend/:people",name:'setamend',component:SetAmend, props:true },
        { path:"setamend",name:'setamend',component:SetAmend },
        { path:"setadministrator",name:'setadministrator',component:SetAdministrator },
        { path:"setadministratoradd/:serial",name:'setadministratoradd',component:SetAdministratorAdd, props:true },
        { path:"setadministratoradd",name:'setadministratoradd',component:SetAdministratorAdd },
        { path:"setlog",name:'setlog',component:SetLog }
      ]
    },
    {
      path:'/*',
      component:Error
    }
  ]
})



