<template>
  <div class="main">
    <header>
      <!-- <h4>项目管理 - 项目列表 - 发放收益</h4> -->
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path:'/manage/projectlist'}">项目管理</el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path:'/manage/projectlist'}">项目列表</el-breadcrumb-item>
        <el-breadcrumb-item>发放收益</el-breadcrumb-item>
      </el-breadcrumb>        
    </header>
    <section>
      <!-- 选项栏部分 -->
      <nav>
        <!-- 用户昵称部分 -->
        <!-- <el-dropdown split-button plain @click="handleClick">
          按条件
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item>用户昵称</el-dropdown-item>
            <el-dropdown-item>手机号码</el-dropdown-item>
            <el-dropdown-item>邮箱</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown> -->
        <el-select v-model="value1" placeholder="按条件">
          <el-option v-for="item in options1" :key="item.value" :label="item.label" :value="item.value">
          </el-option>
        </el-select>
        <!-- 输入框 -->
        <!-- <el-input placeholder="请输入关键字搜索" v-model="search" clearable autofocus>
          <i slot="prefix" class="el-input__icon el-icon-search"></i>
        </el-input> -->
        <el-input placeholder="请输入关键字搜索" prefix-icon="el-icon-search" v-model="search" clearable>
        </el-input>
        <!-- 搜索按钮 -->
        <el-button>搜索</el-button>
        <!-- 类型 -->
        <!-- <el-select v-model="value2" placeholder="类型">
          <el-option v-for="item in options2" :key="item.value" :label="item.label" :value="item.value">
          </el-option>
        </el-select> -->
        <!-- 排序方式 -->
        <el-select v-model="value3" placeholder="默认排序" style="width:200px">
          <el-option v-for="item in options3" :key="item.value" :label="item.label" :value="item.value">
          </el-option>
        </el-select>        
      </nav>
      <!-- 主体部分 -->
      <section>
        <!-- <el-table ref="multipleTable" tooltip-effect="dark" @selection-change="handleSelectionChange" :data="tableData.filter(data => !search || data.number.toLowerCase().includes(search.toLowerCase()) || data.name.toLowerCase().includes(search.toLowerCase()))" style="width: 100%"> -->
        <el-table ref="multipleTable" tooltip-effect="dark" @selection-change="handleSelectionChange" :data="tableData" style="width: 100%">
          <el-table-column type="selection" width="30">
          </el-table-column>
          <el-table-column prop="sequence" label="序号" width="100" sortable>
          </el-table-column>
          <el-table-column label="收益" width="150" sortable>
            <template slot-scope="scope">
              <el-tag size="medium" style="border:0;background-color:transparent;font-weight:bold;" type="danger" v-show="scope.row.earings<0">{{ scope.row.earings }}</el-tag>
              <el-tag size="medium" style="border:0;background-color:transparent;font-weight:bold;" type="success" v-show="scope.row.earings>=0">{{ scope.row.earings }}</el-tag>
            </template>
          </el-table-column>
          <el-table-column label="用户名称" prop="name" width="130" sortable>
          </el-table-column>
          <el-table-column label="电话" prop="phone" width="120" sortable>
          </el-table-column>
          <el-table-column label="项目股权" prop="stock" width="120" sortable>
          </el-table-column>
          <el-table-column label="提交时间" prop="time" width="150" sortable>
          </el-table-column>
        </el-table>
      </section>
      <!-- 分页 -->
      <footer>
        <div class="left-block">
          <el-button type="danger" icon="el-icon-delete" @click="dialogDelete(tableData)">删除</el-button>
          <el-button type="success" icon="el-icon-sort" @click="dialogFormVisible = true">上传新的项目权益</el-button>
        </div>
        <!-- 删除部分的对话框 -->
        <el-dialog title="删除" :visible.sync="dialogVisible" width="30%" :before-close="handleClose" style="text-align:center">
          <span>
            是否确认删除：{{" " + deleteName}}
          </span>
          <span slot="footer" class="dialog-footer">
            <el-button @click="dialogVisible = false">取 消</el-button>
            <el-button type="primary" @click='deleteSelect(tableData)'>确 定</el-button>
          </span>
        </el-dialog>        
        <div class="right-block">
          <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage4" :page-sizes="[100, 200, 300, 400]" :page-size="100" layout="total, sizes, prev, pager, next, jumper" :total="400">
          </el-pagination>
        </div>
      </footer>
      <!-- 对话框部分 -->
      <el-dialog title="发布项目收益" :visible.sync="dialogFormVisible">
        <el-form :model="form">
          <el-form-item label="项目" :label-width="formLabelWidth">
            <el-select v-model="form.region" placeholder="请选择" style="width:300px">
              <el-option label="项目一" value="shanghai"></el-option>
              <el-option label="项目二" value="beijing"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="发放收益（元）" :label-width="formLabelWidth">
            <el-input v-model="form.name" autocomplete="off"></el-input>
          </el-form-item>   
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisible = false">取 消</el-button>
          <el-button type="primary" @click="dialogFormSubmit()">提 交</el-button>
        </div>
      </el-dialog>  
    </section>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        search: '',
        tableData: [{
          sequence: '1',
          name: '大白',
          earings: '+1000',
          phone:13632756286,
          stock:'10%',
          time:'2016-05-02 09：00'
        }, {
          sequence: '2',
          name: '大白',
          earings: '-1000',
          phone:13632756286,
          stock:'10%',
          time:'2016-05-02 09：00'
        }, {
          sequence: '3',
          name: '大白',
          earings: '+1000',
          phone:13632756286,
          stock:'10%',
          time:'2016-05-02 09：00'
        }],
        multipleSelection: [],//保存点击选择框时的数据
        currentPage4: 4,
        UserList: [],
        options1: [{
          value: '1',
          label: '流水号'
        }, {
          value: '2',
          label: '用户名称'
        }],
        value1: '',
        // options2: [{
        //   value: '1',
        //   label: '充值'
        // }, {
        //   value: '2',
        //   label: '提现'
        // }, {
        //   value: '3',
        //   label: '消费'
        // }, {
        //   value: '4',
        //   label: '退款'
        // }],
        // value2: '',
        options3: [{
          value: '1',
          label: '按发生的时间顺序'
        }, {
          value: '2',
          label: '按发生的时间倒序'
        }],
        value3: '',
        dialogFormVisible: false,//写对话框的逻辑
        dialogVisible: false,
        deleteName: [],
        form: {
          name: '',
          address: '',
          person: '',
          phone: '',
          project: '',
          people: ''
        },
        formLabelWidth: '120px'//对话框输入框的宽度  
      }
    },
    created(){
      // this.getUserList();
    },
    methods: {
      // 定义ajax请求数据的方法
      // getGoodsInfo(){
      //   var id=this.id;
      //   var url="goodslist/find?id="+id;
      //   this.$http.get(url).then(result=>{
      //     if(result.body.code==1){
      //       this.UserList=result.body.list;
      //     }
      //   });
      // },
      // handleClick() {
      //   this.$message.error('很皮啊你');        
      // },
      handleSelectionChange(val) {
        this.multipleSelection = val;
        
      },
      deleteRow(index, rows) {
        // console.log(rows[index]);
        rows.splice(index, 1);
      },
      dialogDelete(tableData){
        this.dialogVisible = true;
        var deleteItem=this.multipleSelection;
        this.deleteName.splice(0);//先清空原先数组，再放入
        for(var j=0;j<tableData.length;j++){
          for( var i=0;i<deleteItem.length;i++){
            if(tableData[j] == deleteItem[i]){
              this.deleteName.push(deleteItem[i].name);
            }
          }
        }     
      },
      deleteSelect(tableData){
        let deleteItem=this.multipleSelection;
        // console.log(deleteItem);
        for(let j=0;j<tableData.length;j++){
          // console.log(tableData[j].number)
          for( let i=0;i<deleteItem.length;i++){
            if(tableData[j] == deleteItem[i]){
              // console.log(deleteItem[i].num+"+++");
              // console.log(tableData[j].num); 
              // console.log("===========");
              tableData.splice(j, 1);
              // console.log(tableData);
              // console.log(deleteItem[i].name);
              this.$message({
                type: 'success',
                message: '删除成功: '
              });
            }
          }
        };
        this.dialogVisible = false;
      },
      handleEdit(index, rows){
        this.$router.push("/manage/details");
      },
      handleSizeChange(val) {
        console.log(`每页 ${val} 条`);
      },
      handleCurrentChange(val) {
        console.log(`当前页: ${val}`);
      },
      handleClose(done) {//这一块逻辑是点击对话框之外的区域进行提示是否关闭？
        this.$confirm('确认关闭对话框？')
          .then(_ => {
            done();
          })
          .catch(_ => {});
      },
      dialogFormSubmit(){
        this.$confirm('确认发布项目收益？', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$message({
            type: 'success',
            message: '发布成功!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消发布'
          });   
        }); 
        this.dialogFormVisible =false;       
      }
    }
  }
</script>

<style scoped>
/* 标题部分 */
.main,header{
  width:100%;
  margin-top:-2px;
}
/* 选择栏部分 */
  nav{
    padding:15px;
  }
  /* .el-dropdown {
    vertical-align: top;
  }
  .el-dropdown + .el-dropdown {
    margin-left: 15px;
  } */
  nav div,nav input,nav button{
    margin-left:25px!important;
  }
  .el-icon-arrow-down {
    font-size: 12px;
  }
  .el-input{
    display:inline-block;
    width:300px;
  }
 footer{
    margin:10px;
    padding-right:50px;
    display:flex;   /*设置父元素是弹性布局*/
    justify-content:space-between;
    align-items:center;
 }
  .right-block,.left-block{
    display: inline-block;
  }
  .left-block{
    margin-left:35px;
  }


</style>





