<template>
  <div class="main">
    <header>
      <!-- <h4>项目管理 - 项目列表</h4> -->
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path:'/manage/projectlist'}">项目管理</el-breadcrumb-item>
        <el-breadcrumb-item>项目列表</el-breadcrumb-item>
      </el-breadcrumb>
    </header>
    <section>
      <!-- 选项栏部分 -->
      <nav>
        <!-- 用户昵称部分 -->
        <!-- <el-dropdown split-button plain @click="handleClick">
          按条件
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item>项目名称</el-dropdown-item>
            <el-dropdown-item>发起人</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown> -->
        <el-select v-model="value1" placeholder="按条件">
          <el-option v-for="item in options1" :key="item.value" :label="item.label" :value="item.value">
          </el-option>
        </el-select>
        <!-- 输入框 -->
        <!-- <el-input placeholder="请输入关键字搜索" v-model="search" clearable autofocus>
          <i slot="prefix" class="el-input__icon el-icon-search"></i>
        </el-input> -->
        <el-input placeholder="请输入关键字搜索" prefix-icon="el-icon-search" v-model="search" clearable>
        </el-input>
        <!-- 项目状态部分 -->
        <!-- <el-dropdown split-button plain @click="handleClick">
          项目状态
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item>已完成</el-dropdown-item>
            <el-dropdown-item>待审核</el-dropdown-item>
            <el-dropdown-item>未完成</el-dropdown-item>
            <el-dropdown-item>失败</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown> -->
        <!-- 搜索按钮 -->
        <el-button>搜索</el-button>        
        <!-- 排序方式 -->
        <!-- <el-dropdown split-button plain @click="handleClick">
          默认排序
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item>按发布时间倒序</el-dropdown-item>
            <el-dropdown-item>按发布时间顺序</el-dropdown-item>
            <el-dropdown-item>按目标金额大小倒序</el-dropdown-item>
            <el-dropdown-item>按目标金额大小顺序</el-dropdown-item>
            <el-dropdown-item>按已筹资金大小倒序</el-dropdown-item>
            <el-dropdown-item>按已筹资金大小顺序</el-dropdown-item>
            <el-dropdown-item>按参与人数高低倒序</el-dropdown-item>
            <el-dropdown-item>按参与人数高低顺序</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown> -->
        <el-select v-model="value2" placeholder="默认排序" style="width:200px">
          <el-option v-for="item in options2" :key="item.value" :label="item.label" :value="item.value">
          </el-option>
        </el-select>
        <!-- 发布项目 -->
        <el-button type="success" icon="el-icon-upload" style="float:right;margin-right:150px;">发布项目</el-button>
      </nav>
      <!-- 主体部分 -->
      <section>
        <!-- <el-table ref="multipleTable" tooltip-effect="dark" @selection-change="handleSelectionChange" :data="tableData.filter(data => !search || data.number.toLowerCase().includes(search.toLowerCase()) || data.name.toLowerCase().includes(search.toLowerCase()))" style="width: 100%"> -->
        <el-table ref="multipleTable" tooltip-effect="dark" @selection-change="handleSelectionChange" :data="tableData" style="width: 100%">
          <el-table-column type="selection" width="30">
          </el-table-column>
          <el-table-column label="项目分类" prop="class" width="120" sortable>
          </el-table-column>   
          <el-table-column label="项目名称" prop="name" width="120" sortable>
          </el-table-column>                 
          <el-table-column prop="user" label="发起人" width="120" sortable>
          </el-table-column>
          <el-table-column label="项目所在地" prop="location" width="150" sortable>
          </el-table-column>
          <el-table-column label="目标的金额(万元)" prop="money" width="200" sortable>
          </el-table-column>
          <!-- <el-table-column prop="progress" label="进度" width="80" sortable>
          </el-table-column>
          <el-table-column prop="done" label="已认筹(元)" width="150" sortable>
          </el-table-column>
          <el-table-column prop="people" label="参与人数(人)" width="130" sortable>
          </el-table-column>
          <el-table-column label="剩余时间(天)" prop="residue" width="130" sortable>
          </el-table-column>          
          <el-table-column label="项目状态" prop="state" width="100" sortable>
          </el-table-column> -->
          <el-table-column label="发布时间" prop="date" width="150" sortable>
          </el-table-column>    
          <el-table-column fixed="right" label="操作" width="300">
            <template slot-scope="scope">
              <el-button @click.native.prevent="details(scope.$index, tableData)" type="text" size="small">
                详情
              </el-button>
              <el-button @click.native.prevent="suggest(scope.$index, tableData)" type="text" size="small">
                股权信息
              </el-button>
              <el-button @click.native.prevent="personnel(scope.$index, tableData)" type="text" size="small">
                公告
              </el-button>
              <el-button @click.native.prevent="group(scope.$index, tableData)" type="text" size="small">
                收益
              </el-button>                         
            </template>
          </el-table-column>
        </el-table>
      </section>
      <!-- 分页 -->
      <footer>
        <div class="left-block">
          <el-button type="danger" icon="el-icon-delete" @click="dialogDelete(tableData)">删除</el-button>
          <el-button type="info" icon="el-icon-warning">下架</el-button>
        </div>
        <!-- 对话框 -->
        <el-dialog title="删除" :visible.sync="dialogVisible" width="30%" :before-close="handleClose" style="text-align:center">
          <span>
            是否确认删除：{{" " + deleteName}}
          </span>
          <span slot="footer" class="dialog-footer">
            <el-button @click="dialogVisible = false">取 消</el-button>
            <el-button type="primary" @click='deleteSelect(tableData)'>确 定</el-button>
          </span>
        </el-dialog>        
        <div class="right-block">
          <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage4" :page-sizes="[100, 200, 300, 400]" :page-size="100" layout="total, sizes, prev, pager, next, jumper" :total="400">
          </el-pagination>
        </div>
      </footer>
    </section>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        search: '',
        tableData: [{
          class:'充电桩',
          name: '一号充电桩',
          user:'熊大',
          location:'广东-深圳-南山',
          money:'1100000045',
          progress:'100%',
          done:'10000000',
          people:'99',
          residue:'3',
          date:'2016-05-22 10:00',
          state:'待审核'
        }, {
          class:'充电桩',
          name: '二号充电桩',
          user:'小白',
          location:'广东-深圳-福田',
          money:'7770000045',
          progress:'50%',
          done:'456000000',
          people:'89',
          residue:'2',
          date:'2015-05-12 18:10',
          state:'进行中'
        }, {
          class:'充电桩',
          name: '三号充电桩',
          user:'小蓝',
          location:'广东-深圳-龙华',
          money:'6660000045',
          progress:'80%',
          done:'99000000',
          people:'59',
          residue:'1',
          date:'2017-05-02 12:30',
          state:'已完成'
        }, {
          class:'充电桩',
          name: '四号充电桩',
          user:'小绿',
          location:'广东-深圳-布吉',
          money:'5550000045',
          progress:'70%',
          done:'188000000',
          people:'100',
          residue:'2',
          date:'2018-05-05 11:00',
          state:'失败'
        }],
        multipleSelection: [],
        currentPage1: 5,
        currentPage2: 5,
        currentPage3: 5,
        currentPage4: 4,
        options1: [{
          value: '1',
          label: '项目名称'
        }, {
          value: '2',
          label: '参与人'
        }, {
          value: '3',
          label: '公司'
        }],
        value1: '',
        options2: [{
          value: '1',
          label: '按发布时间倒序'
        }, {
          value: '2',
          label: '按发布时间顺序'
        },{
          value: '3',
          label: '按目标金额大小倒序'
        }, {
          value: '4',
          label: '按目标金额大小顺序'
        },{
          value: '5',
          label: '按已筹资金大小倒序'
        }, {
          value: '6',
          label: '按已筹资金大小顺序'
        },{
          value: '7',
          label: '按参与人数高低倒序'
        }, {
          value: '8',
          label: '按参与人数高低顺序'
        }],
        value2: '',
        dialogVisible: false,
        deleteName: [] 
      }
    },
    methods: {
      handleClick() {
        this.$message.error('别点那里。。');
      },
      handleSelectionChange(val) {
        this.multipleSelection = val;
      },
      deleteRow(index, rows) {

      },
      dialogDelete(tableData){
        this.dialogVisible = true;
        var deleteItem=this.multipleSelection;
        this.deleteName.splice(0);//先清空原先数组，再放入
        for(var j=0;j<tableData.length;j++){
          for( var i=0;i<deleteItem.length;i++){
            if(tableData[j] == deleteItem[i]){
              this.deleteName.push(deleteItem[i].name);
            }
          }
        }     
      },
      deleteSelect(tableData){
        let deleteItem=this.multipleSelection;
        // console.log(deleteItem);
        if(tableData.sort().toString() == deleteItem.sort().toString()){
          tableData.splice(0, tableData.length);
        }else{
          for(let j=0;j<tableData.length;j++){
          // console.log(tableData[j].number)
            for( let i=0;i<deleteItem.length;i++){
              if(tableData[j] == deleteItem[i]){
                // console.log(deleteItem[i].num+"+++");
                // console.log(tableData[j].num); 
                // console.log("===========");
                tableData.splice(j, 1);
                // console.log(tableData);
                // console.log(deleteItem[i].name);
                this.$message({
                  type: 'success',
                  message: '删除成功: '
                });
              }
            }
          }
        };
        this.dialogVisible = false;
      },
      handleEdit(index, rows){
        
      },
      details(){
        this.$router.push("/manage/projectdetails");
      },
      suggest(){
        this.$router.push("/manage/projectequity");
      },
      personnel(){
        this.$router.push("/manage/projectnotice");
      },
      group(){
        this.$router.push("/manage/projectearing");
      },
      handleSizeChange(val) {
        console.log(`每页 ${val} 条`);
      },
      handleCurrentChange(val) {
        console.log(`当前页: ${val}`);
      },
      handleClose(done) {//这一块逻辑是点击对话框之外的区域进行提示是否关闭？
        this.$confirm('确认关闭对话框？')
          .then(_ => {
            done();
          })
          .catch(_ => {});
      }      
    }
  }
</script>

<style scoped>
/* 标题部分 */
.main,header{
  width:100%;
  margin-top:-2px;
}

/* 选择栏部分 */
  nav{
    padding:15px;
  }
  .el-dropdown {
    vertical-align: top;
  }
  .el-dropdown + .el-dropdown {
    margin-left: 15px;
  }
  nav div,nav input,nav button{
    margin-left:25px!important;
  }
  nav div:last-child{
    margin-left:100px!important;
  }
  .el-icon-arrow-down {
    font-size: 12px;
  }
  .el-input{
    display:inline-block;
    width:300px;
  }
/* 栅格布局 */
 footer{
    margin:10px;
    padding-right:50px;
    display:flex;   /*设置父元素是弹性布局*/
    justify-content:space-between;
    align-items:center;
 }
  .right-block,.left-block{
    display: inline-block;
  }
  .left-block{
    margin-left:35px;
  }
 
</style>



