<template>
  <div class="main">
    <header>
      <!-- <h4>系统设置 - 权限管理</h4> -->
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path:'/manage/setjurisdiction'}">系统设置</el-breadcrumb-item>
        <el-breadcrumb-item>权限管理</el-breadcrumb-item>    
      </el-breadcrumb>   
    </header>
    <nav>
      权限信息
    </nav>
    <section>
      <el-table :data="tableData" style="width: 100%;">
        <el-table-column prop="people" label="角色" width="280">
        </el-table-column>        
        <el-table-column prop="list" label="对应权限列表" width="1000">
          <!-- <el-table-column v-for="item in list" :key="item.value" v-text="item"></el-table-column> -->
          <template slot-scope="scope">
            <span size="medium" v-for="s in scope.row.list" :key="s.value" v-text="s" style="padding:10px"></span>
          </template>
        </el-table-column>
        <el-table-column label="操作" width="120">
            <template slot-scope="scope">
              <el-button @click.native.prevent="deleteRow(scope.$index, tableData)" type="text" style="color:red;" size="small">
                删除
              </el-button>
              <el-button @click.native.prevent="handleEdit(scope.$index, tableData)" type="text" size="small">
                修改
              </el-button>
            </template>
          </el-table-column>
      </el-table>
    </section>
    <footer style="padding-top:80px;background:#FFFFFF">
      <el-button plain type="success" style="margin-left:80px" @click.native.prevent="handleJump()">
        新增权限
      </el-button>
    </footer>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        tableData: [{
          people: '路飞',
          list: ["客户信息","员工信息","所有订单信息"]
        }, {
          people: '索隆',
          list: ["客户信息","员工信息","所有订单信息"]
        }, {
          people: '香吉士',
          list: ["客户信息","员工信息","所有订单信息"]
        }], 
        deleteName: [{
        }]
      }
    },
    methods:{
      handleEdit(index, rows){
        // console.log(rows[index].people);
        this.$router.push("/manage/setamend/"+rows[index].people);
      },
      handleJump(){
        this.$router.push("/manage/setamend");
        console.log(123);
      },
      deleteRow(index, rows) {
        // console.log(rows[index].people);
        // rows.splice(index, 1);
        // this.dialogVisible = true;
        this.deleteName = rows[index].people;
        this.$confirm(`此操作将永久删除 ${this.deleteName} 的账户信息, 是否继续?`, '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          rows.splice(index, 1);
          this.$message({
            type: 'success',
            message: '删除成功!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });          
        });
      }
    }
  }
</script>

<style scoped>
/* 标题部分 */
.main,header{
  width:100%;
  margin-top:-2px;
}

/* 项目名称 */
.main>nav:nth-child(2){
  font-weight: bold;
  padding:10px 20px 20px 20px;
  background-color:#fff;
}

.main>nav:nth-child(2)>span{
  color:#5167B7;
}


</style>


