// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import "@/assets/css/base.css"
import ElementUI from 'element-ui';
//1.1加载模块 vueResource(发送ajax请求) //vue-resource是库名，大小写不能变
import VueResource from 'vue-resource';
//1.2:将VueResource  注册vue  //这里用use是因为要引用VueResource所有组件
Vue.use(VueResource);
//1.2.1:设置请求根路径（服务器地址）(这样以后就不用写开头的路径，因为根路径可能会变)
Vue.http.options.root="http://127.0.0.1:3000/";
//1.2.2:创建全局过滤器
Vue.filter("datetimeFilter",function(val){
  var date=new Date(val);
  var y=date.getFullYear();
  var m=date.getMonth()+1;
  m<10&&(m="0"+m);
  var d=date.getDate();
  d<10&&(d="0"+d);
  var h=date.getHours();
  h>12&&(h-=12);
  h<10&&(h="0"+h);
  var M=date.getMinutes()
  M<10&&(M="0"+M);
  return `${y}-${m}-${d} ${h}:${M}`;
});

// 引入elementui
import 'element-ui/lib/theme-chalk/index.css';
Vue.use(ElementUI);

Vue.config.productionTip = false

// 引入animate.css  https://www.cnblogs.com/rlann/p/7192535.html
import animate from 'animate.css'
Vue.use(animate);

//2.1:将vuex添加到项目中
//第一步：导入vuex
import Vuex from "vuex";
//第二步：将vues注册vue
Vue.use(Vuex);
//第三步：创建vuex实例[共享数据;提供操作数据方法;获取数据方法]
//new Vuex.Store();是指得到一个数据仓储对象
var store=new Vuex.Store({
  state:{//专门用来存储共享数据，想象组件data
    //如果在组件中操作 this.$store.state.***方法
    //#购物车中数量共享数据
    count:1,
    counts:0
  },
  mutations:{
    //#注意  操作state共享数据只能通过 mutations提供方法
    //如果组件想调用mutations 只能使用
    //this.$store.commit("increment");
    numUpdate(state){
      state.counts=state.counts+state.count;
      state.count=1
    },
    increment(state){state.count++},
    subtract(state){
      if(state.count>1){
        state.count--
      }
    }//increment和subtract是自己取的方法名
  },
  getters:{//获取数据优先用getters方法
    //#getters  只负责对外提供数据，不负责修改数据
    //在组件模板中{{$store.getters.optCount}}
    optCount:function(state){
      return state.count;
    },
    optCounts:function(state){
      return state.counts
    }
  }
});

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  store,
  template: '<App/>'
})



