<template>
  <div class="main">
    <header>
      <!-- <h4>企业管理 - 企业列表</h4> -->
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path:'/manage/companylist'}">企业管理</el-breadcrumb-item>
        <el-breadcrumb-item>企业列表</el-breadcrumb-item>
      </el-breadcrumb>  
    </header>
    <section>
      <!-- 选项栏部分 -->
      <nav>
        <el-select v-model="value1" placeholder="按条件">
          <el-option v-for="item in options1" :key="item.value" :label="item.label" :value="item.value">
          </el-option>
        </el-select>
        <!-- 输入框 -->
        <el-input placeholder="请输入关键字搜索" prefix-icon="el-icon-search" v-model="search" clearable>
        </el-input>
        <!-- 搜索按钮 -->
        <el-button>搜索</el-button>
        <!-- 排序方式 -->
        <el-select v-model="value2" placeholder="默认排序" style="width:200px">
          <el-option v-for="item in options2" :key="item.value" :label="item.label" :value="item.value">
          </el-option>
        </el-select>
      </nav>
      <!-- 主体部分 -->
      <section>
        <!-- <el-table ref="multipleTable" tooltip-effect="dark" @selection-change="handleSelectionChange" :data="tableData.filter(data => !search || data.number.toLowerCase().includes(search.toLowerCase()) || data.name.toLowerCase().includes(search.toLowerCase()))" style="width: 100%"> -->
        <el-table ref="multipleTable" tooltip-effect="dark" @selection-change="handleSelectionChange" :data="tableData" style="width: 100%">
          <el-table-column type="selection" width="30">
          </el-table-column>
          <el-table-column prop="name" label="企业名称" width="100" sortable>
          </el-table-column>
          <el-table-column label="地址" prop="address" width="300" sortable>
          </el-table-column>
          <el-table-column label="法人" prop="person" width="130" sortable>
          </el-table-column>          
          <el-table-column label="联系方式" prop="phone" width="150" sortable>
          </el-table-column>
          <el-table-column label="投资项目（个）" prop="project" width="150" sortable>
          </el-table-column>
          <el-table-column label="绑定人员数量" prop="people" width="150" sortable>
          </el-table-column>
          <el-table-column label="总计收益" prop="earing" width="150" sortable>
            </el-table-column>
          <el-table-column fixed="right" label="操作" width="300">
            <template slot-scope="scope">
              <el-button @click.native.prevent="handleEdit(scope.$index, tableData)" type="text" size="small">
                编辑
              </el-button>
              <router-link to="/manage/componentparticipation">分红记录</router-link>
              <router-link to="/manage/componentproject">投资项目</router-link>
            </template>
          </el-table-column>          
        </el-table>
      </section>
      <!-- 分页 -->
      <footer>
        <div class="left-block">
          <el-button type="danger" icon="el-icon-delete" @click="dialogDelete(tableData)">删除</el-button>
          <el-button type="success" icon="el-icon-plus" @click.native.prevent="handleEdit()">新增企业</el-button>
        </div>
        <!-- 对话框 -->
        <el-dialog title="删除" :visible.sync="dialogVisible" width="30%" :before-close="handleClose" style="text-align:center">
          <span>
            是否确认删除：{{" " + deleteName}}
          </span>
          <span slot="footer" class="dialog-footer">
            <el-button @click="dialogVisible = false">取 消</el-button>
            <el-button type="primary" @click='deleteSelect(tableData)'>确 定</el-button>
          </span>
        </el-dialog>        
        <div class="right-block">
          <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage4" :page-sizes="[100, 200, 300, 400]" :page-size="100" layout="total, sizes, prev, pager, next, jumper" :total="400">
          </el-pagination>
        </div>
      </footer>
      <!-- 对话框 -->
      <el-dialog :title="dio_title" :visible.sync="dialogFormVisible">
        <el-form :model="form">
          <el-form-item label="企业名称*：" :label-width="formLabelWidth">
            <el-input v-model="form.name" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="地址*：" :label-width="formLabelWidth">
            <el-input v-model="form.address" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="法人*：" :label-width="formLabelWidth">
            <el-input v-model="form.person" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="联系方式*：" :label-width="formLabelWidth">
            <el-input v-model="form.phone" autocomplete="off"></el-input>
          </el-form-item>                    
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisible = false">取 消</el-button>
          <el-button type="primary" @click="dialogFormVisible = false">确 定</el-button>
        </div>
      </el-dialog>      
    </section>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        search: '',
        tableData: [{
          name: '绿色庄园',
          address: '深圳南山区后海大道深圳大学',
          person: '大熊',
          phone:13632756286,
          project:'23',
          people: '2',
          earing: '50000.00'
        }, {
          name: '绿色庄园',
          address: '深圳南山区后海大道深圳大学',
          person: '大熊',
          phone:'0755-71979843',
          project:'23',
          people: '8',
          earing: '150000.00'
        }, {
          name: '绿色庄园',
          address: '深圳南山区后海大道深圳大学',
          person: '大熊',
          phone:13632756286,
          project:'23',
          people: '5',
          earing: '21000.00'
        }],
        multipleSelection: [],//保存点击选择框时的数据
        currentPage4: 4,
        UserList: [],
        options1: [{
          value: '1',
          label: '企业'
        }],
        value1: '',
        options2: [{
          value: '1',
          label: '按参与项目数量倒序'
        }, {
          value: '2',
          label: '按参与项目数量顺序'
        }],
        value2: '',
        dialogFormVisible: false,//写对话框的逻辑
        dialogVisible: false,
        deleteName: [],
        dio_title:'',
        form: {
          name: '',
          address: '',
          person: '',
          phone: '',
          project: '',
          people: ''
        },
        formLabelWidth: '120px'//对话框输入框的宽度
      }
    },
    created(){
      // this.getUserList();
    },
    methods: {
      // 定义ajax请求数据的方法
      // getGoodsInfo(){
      //   var id=this.id;
      //   var url="goodslist/find?id="+id;
      //   this.$http.get(url).then(result=>{
      //     if(result.body.code==1){
      //       this.UserList=result.body.list;
      //     }
      //   });
      // },
      // handleClick() {
      //   this.$message.error('很皮啊你');        
      // },
      handleSelectionChange(val) {
        this.multipleSelection = val;
        
      },
      deleteRow(index, rows) {
        // console.log(rows[index]);
        rows.splice(index, 1);
      },
      dialogDelete(tableData){
        this.dialogVisible = true;
        var deleteItem=this.multipleSelection;
        this.deleteName.splice(0);//先清空原先数组，再放入
        for(var j=0;j<tableData.length;j++){
          for( var i=0;i<deleteItem.length;i++){
            if(tableData[j] == deleteItem[i]){
              this.deleteName.push(deleteItem[i].name);
            }
          }
        }     
      },
      deleteSelect(tableData){
        let deleteItem=this.multipleSelection;
        // console.log(deleteItem);
        if(tableData.sort().toString() == deleteItem.sort().toString()){
          tableData.splice(0, tableData.length);
        }else{
          for(let j=0;j<tableData.length;j++){
          // console.log(tableData[j].number)
            for( let i=0;i<deleteItem.length;i++){
              if(tableData[j] == deleteItem[i]){
                // console.log(deleteItem[i].num+"+++");
                // console.log(tableData[j].num); 
                // console.log("===========");
                tableData.splice(j, 1);
                // console.log(tableData);
                // console.log(deleteItem[i].name);
                this.$message({
                  type: 'success',
                  message: '删除成功: '
                });
              }
            }
          }
        };
        this.dialogVisible = false;
      },
      handleEdit(index, rows){
        // this.$router.push("/manage/details");
        // console.log(rows[index]);
        this.dialogFormVisible = true;
        if(rows != undefined){
          this.dio_title = "编辑企业";
          this.form.name = rows[index].name;
          this.form.address = rows[index].address;
          this.form.person = rows[index].person;
          this.form.phone = rows[index].phone;          
        }else{
          this.dio_title = "新增企业";          
          this.form.name = '';
          this.form.address = '';
          this.form.person = '';
          this.form.phone = '';       
        }
      },
      handleSizeChange(val) {
        console.log(`每页 ${val} 条`);
      },
      handleCurrentChange(val) {
        console.log(`当前页: ${val}`);
      },
      handleClose(done) {//这一块逻辑是点击对话框之外的区域进行提示是否关闭？
        this.$confirm('确认关闭对话框？')
          .then(_ => {
            done();
          })
          .catch(_ => {});
      }      
    }
  }
</script>

<style scoped>
/* 标题部分 */
.main,header{
  width:100%;
  margin-top:-2px;
}
/* 选择栏部分 */
  nav{
    padding:15px;
  }
  /* .el-dropdown {
    vertical-align: top;
  }
  .el-dropdown + .el-dropdown {
    margin-left: 15px;
  } */
  nav div,nav input,nav button{
    margin-left:25px!important;
  }
  .el-icon-arrow-down {
    font-size: 12px;
  }
  .el-input{
    display:inline-block;
    width:300px;
  }
 footer{
    margin:10px;
    padding-right:50px;
    display:flex;   /*设置父元素是弹性布局*/
    justify-content:space-between;
    align-items:center;
 }
  .right-block,.left-block{
    display: inline-block;
  }
  .left-block{
    margin-left:35px;
  }

</style>



