<template>
  <div class="main">
    <header>
      <!-- <h4>系统设置 - 管理员管理</h4> -->
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path:'/manage/setadministrator'}">系统设置</el-breadcrumb-item>
        <el-breadcrumb-item>管理员管理</el-breadcrumb-item>
      </el-breadcrumb>       
    </header>
    <nav>
      账户信息
    </nav>
    <section>
      <el-table :data="tableData" style="width: 100%;">
        <el-table-column prop="serial" label="用户编号" width="150">
        </el-table-column>        
        <el-table-column prop="people" label="角色" width="120">
        </el-table-column> 
        <el-table-column prop="name" label="名字" width="120">
        </el-table-column> 
        <el-table-column prop="phone" label="手机号" width="120">
        </el-table-column> 
        <el-table-column prop="state" label="状态" width="120">
        </el-table-column>                         
        <el-table-column label="操作" width="150">
            <template slot-scope="scope">
              <el-button @click.native.prevent="deleteRow(scope.$index, tableData)" type="text" style="color:red;" size="small">
                删除
              </el-button> 
              <el-button @click.native.prevent="handleEdit(scope.$index, tableData)" type="text" size="small">
                修改
              </el-button>           
            </template>
          </el-table-column>
      </el-table>
    </section>
    <footer style="padding-top:30px;background:#FFFFFF">
      <el-button plain type="success" style="margin-left:80px" @click.native.prevent="handleJump()">
        新增账户
      </el-button>
    </footer>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        tableData: [{
          serial: 'CS2017',
          people: '管理员',
          name: '大熊哥',
          phone: '13632756286',
          state: '正常'
        }, {
          serial: 'CS2013',
          people: '管理员',
          name: '中熊哥',
          phone: '13632756286',
          state: '正常'
        }, {
          serial: 'CS2019',
          people: '管理员',
          name: '小熊哥',
          phone: '13632756286',
          state: '正常'
        }],
        dialogVisible: false,
        deleteName: [{

        }]
      }
    },
    methods:{
      handleEdit(index, rows){
        // console.log(rows[index].people);
        this.$router.push("/manage/setadministratoradd/"+rows[index].serial);
      },
      handleJump(){
        this.$router.push("/manage/setadministratoradd");
        console.log(123);
      },
      deleteRow(index, rows) {
        console.log(rows[index].name);
        // rows.splice(index, 1);
        // this.dialogVisible = true;
        this.deleteName = rows[index].name;
        this.$confirm(`此操作将永久删除 ${this.deleteName} 的账户信息, 是否继续?`, '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          rows.splice(index, 1);
          this.$message({
            type: 'success',
            message: '删除成功!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });          
        });
      }
    }
  }
</script>

<style scoped>
/* 标题部分 */
.main,header{
  width:100%;
  margin-top:-2px;
}

/* 项目名称 */
.main>nav:nth-child(2){
  font-weight: bold;
  padding:10px 20px 20px 20px;
  background-color:#fff;
}

.main>nav:nth-child(2)>span{
  color:#5167B7;
}


</style>


