<template>
  <div class="main">
    <header>
      <!-- <h4>用户管理 - 用户列表</h4> -->
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path:'/manage'}">用户管理</el-breadcrumb-item>
        <el-breadcrumb-item>用户列表</el-breadcrumb-item>
      </el-breadcrumb>
    </header>
    <section>
      <!-- 选项栏部分 -->
      <nav>
        <!-- 用户昵称部分 -->
        <!-- <el-dropdown split-button plain @click="handleClick">
          按条件
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item>用户昵称</el-dropdown-item>
            <el-dropdown-item>手机号码</el-dropdown-item>
            <el-dropdown-item>邮箱</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown> -->
        <el-select v-model="value1" placeholder="按条件">
          <el-option v-for="item in options1" :key="item.value" :label="item.label" :value="item.value">
          </el-option>
        </el-select>
        <!-- 输入框 -->
        <!-- <el-input placeholder="请输入关键字搜索" v-model="search" clearable autofocus>
          <i slot="prefix" class="el-input__icon el-icon-search"></i>
        </el-input> -->
        <el-input placeholder="请输入关键字搜索" prefix-icon="el-icon-search" v-model="search" clearable>
        </el-input>        
        <!-- 实名状态部分 -->
        <!-- <el-dropdown split-button plain @click="handleClick">
          实名状态
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item>已完成</el-dropdown-item>
            <el-dropdown-item>未完成</el-dropdown-item>
            <el-dropdown-item>失败</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown> -->
        <el-select v-model="value2" placeholder="实名状态">
          <el-option v-for="item in options2" :key="item.value" :label="item.label" :value="item.value">
          </el-option>
        </el-select>
        <!-- 评测部分 -->
        <!-- <el-dropdown split-button plain @click="handleClick">
          评测等级
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item>成长型</el-dropdown-item>
            <el-dropdown-item>保守型</el-dropdown-item>
            <el-dropdown-item>风险型</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown> -->
        <el-select v-model="value3" placeholder="评测等级">
          <el-option v-for="item in options3" :key="item.value" :label="item.label" :value="item.value">
          </el-option>
        </el-select>
        <!-- 搜索按钮 -->
        <el-button>搜索</el-button>
        <!-- 排序方式 -->
        <!-- <el-dropdown split-button plain @click="handleClick">
          默认排序
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item>按登录次数倒序</el-dropdown-item>
            <el-dropdown-item>按登录次数顺序</el-dropdown-item>
            <el-dropdown-item>按资金大小倒序</el-dropdown-item>
            <el-dropdown-item>按资金大小顺序</el-dropdown-item>
            <el-dropdown-item>按积分高低倒序</el-dropdown-item>
            <el-dropdown-item>按积分高低顺序</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown> -->
        <el-select v-model="value4" placeholder="默认排序" style="width:200px">
          <el-option v-for="item in options4" :key="item.value" :label="item.label" :value="item.value">
          </el-option>
        </el-select>        
      </nav>
      <!-- 主体部分 -->
      <section>
        <!-- <el-table ref="multipleTable" tooltip-effect="dark" @selection-change="handleSelectionChange" :data="tableData.filter(data => !search || data.number.toLowerCase().includes(search.toLowerCase()) || data.name.toLowerCase().includes(search.toLowerCase()))" style="width: 100%"> -->
        <el-table ref="multipleTable" tooltip-effect="dark" @selection-change="handleSelectionChange" :data="tableData" style="width: 100%">
          <el-table-column type="selection" width="30">
          </el-table-column>
          <el-table-column prop="name" label="用户名称" width="120" sortable>
          </el-table-column>
          <el-table-column label="手机号码" prop="number" width="130" sortable>
          </el-table-column>
          <!-- <el-table-column prop="email" label="邮箱" width="180" sortable>
          </el-table-column> 
          <el-table-column prop="balance" label="余额(元)" width="120" sortable>
          </el-table-column>
          <el-table-column label="积分" prop="integral" width="100" sortable>
          </el-table-column>-->
          <el-table-column label="注册时间" prop="register" width="150" sortable>
          </el-table-column>
          <el-table-column label="上次登录时间" prop="enter" width="150" sortable>
          </el-table-column>
          <el-table-column label="登录次数" prop="num" width="120" sortable>
          </el-table-column>
          <!-- <el-table-column label="实名认证状态" prop="state" width="130" sortable>
          </el-table-column>
          <el-table-column label="评测等级" prop="evaluating" width="120" sortable>
          </el-table-column> -->
          <el-table-column label="参与项目数量" prop="project" width="150" sortable>
          </el-table-column>
          <!-- <el-table-column label="累积投资金额(元)" prop="money" width="160" sortable>
          </el-table-column> -->
          <el-table-column fixed="right" label="操作" width="300">
            <template slot-scope="scope">
              <!-- <el-button @click.native.prevent="handleEdit(scope.$index, tableData)" type="text" size="small">
                所属公司
              </el-button> -->
              <router-link to="/manage/usercompany">所属公司</router-link>
              <!-- <el-button @click.native.prevent="deleteRow(scope.$index, tableData)" type="text" size="small">
                查看分红
              </el-button> -->
              <router-link to="/manage/userparticipation">查看分红</router-link>
              <!-- <el-button @click.native.prevent="deleteRow(scope.$index, tableData)" type="text" size="small">
                参与项目
              </el-button> -->
              <router-link to="/manage/userproject">参与项目</router-link>
            </template>
          </el-table-column>
        </el-table>
      </section>
      <!-- 分页 -->
      <footer>
        <div class="left-block">
          <el-button type="danger" icon="el-icon-delete" @click="dialogDelete(tableData)">删除</el-button>
        </div>
        <!-- 对话框 -->
        <el-dialog title="删除" :visible.sync="dialogVisible" width="30%" :before-close="handleClose" style="text-align:center">
          <span>
            是否确认删除：{{" " + deleteName}}
          </span>
          <span slot="footer" class="dialog-footer">
            <el-button @click="dialogVisible = false">取 消</el-button>
            <el-button type="primary" @click='deleteSelect(tableData)'>确 定</el-button>
          </span>
        </el-dialog>
        <!-- 分页 -->
        <div class="right-block">
          <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage4" :page-sizes="[100, 200, 300, 400]" :page-size="100" layout="total, sizes, prev, pager, next, jumper" :total="400">
          </el-pagination>
        </div>
      </footer>
    </section>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        search: '',
        tableData: [{
          number: '15013505300',
          name: '大白',
          email: '1044136908@qq.com',
          balance:500000.00,
          integral:100,
          register:'2016-05-02 09：00',
          enter:'2019-2-19 09：00',
          num:'10',
          state:'已完成',
          evaluating:'成长型',
          project:'3',
          money:'1000000.00'
        }, {
          number: '13801350530',
          name: '大熊',
          email: '1044136908@qq.com',
          balance:5000.00,
          integral:100,
          register:'2016-05-01 09：00',
          enter:'2019-2-28 09：00',
          num:'12',
          state:'已完成',
          evaluating:'成长型',
          project:'5',
          money:'50000.00'
        }, {
          number: '17801350530',
          name: '月城',
          email: '844136908@qq.com',
          balance:5000.00,
          integral:100,
          register:'2016-05-01 09：00',
          enter:'2019-2-28 09：00',
          num:'12',
          state:'未完成',
          evaluating:'成长型',
          project:'8',
          money:'560000.00'
        }, {
          number: '15633350530',
          name: '小白',
          email: '744136908@qq.com',
          balance:5000.00,
          integral:100,
          register:'2016-05-01 09：00',
          enter:'2019-2-28 09：00',
          num:'12',
          state:'已完成',
          evaluating:'成长型',
          project:'5',
          money:'150000.00'
        }, {
          number: '18013505300',
          name: '百晓',
          email: '644136908@qq.com',
          balance:11500000.00,
          integral:300,
          register:'2016-04-02 09：00',
          enter:'2019-1-19 09：00',
          num:'11',
          state:'已完成',
          evaluating:'成长型',
          project:'8',
          money:'2150000.00'
        }],
        multipleSelection: [],//保存点击选择框时的数据
        currentPage4: 4,
        UserList: [],
        options1: [{
          value: '1',
          label: '用户昵称'
        }, {
          value: '2',
          label: '手机号码'
        }, {
          value: '3',
          label: '邮箱'
        }],
        value1: '',
        options2: [{
          value: '1',
          label: '已完成'
        }, {
          value: '2',
          label: '未完成'
        }, {
          value: '3',
          label: '失败'
        }],
        value2: '',
        options3: [{
          value: '1',
          label: '成长型'
        }, {
          value: '2',
          label: '保守型'
        }, {
          value: '3',
          label: '风险型'
        }],
        value3: '',
        options4: [{
          value: '1',
          label: '按登录次数倒序'
        }, {
          value: '2',
          label: '按登录次数顺序'
        }, {
          value: '3',
          label: '按资金大小倒序'
        },{
          value: '4',
          label: '按资金大小顺序'
        }, {
          value: '5',
          label: '按积分高低倒序'
        }, {
          value: '6',
          label: '按积分高低顺序'
        }],
        value4: '',
        dialogVisible: false,
        deleteName: []   
      }
    },
    created(){
      // this.getUserList();
    },
    methods: {
      // 定义ajax请求数据的方法
      // getGoodsInfo(){
      //   var id=this.id;
      //   var url="goodslist/find?id="+id;
      //   this.$http.get(url).then(result=>{
      //     if(result.body.code==1){
      //       this.UserList=result.body.list;
      //     }
      //   });
      // },
      // handleClick() {
      //   this.$message.error('很皮啊你');        
      // },
      handleSelectionChange(val) {
        this.multipleSelection = val;
      },
      // deleteRow(index, rows) {
      //   // console.log(rows[index]);
      //   rows.splice(index, 1);
      // },
      dialogDelete(tableData){
        this.dialogVisible = true;
        var deleteItem=this.multipleSelection;
        this.deleteName.splice(0);//先清空原先数组，再放入
        for(var j=0;j<tableData.length;j++){
          for( var i=0;i<deleteItem.length;i++){
            if(tableData[j] == deleteItem[i]){
              this.deleteName.push(deleteItem[i].name);
            }
          }
        }     
      },
      deleteSelect(tableData){
        let deleteItem=this.multipleSelection;
        // console.log(deleteItem);
        if(tableData.sort().toString() == deleteItem.sort().toString()){
          tableData.splice(0, tableData.length);
        }else{
          for(let j=0;j<tableData.length;j++){
          // console.log(tableData[j].number)
            for( let i=0;i<deleteItem.length;i++){
              if(tableData[j] == deleteItem[i]){
                // console.log(deleteItem[i].num+"+++");
                // console.log(tableData[j].num); 
                // console.log("===========");
                tableData.splice(j, 1);
                // console.log(tableData);
                // console.log(deleteItem[i].name);
                this.$message({
                  type: 'success',
                  message: '删除成功: '
                });
              }
            }
          }
        };
        this.dialogVisible = false;
      },
      handleEdit(index, rows){
        this.$router.push("/manage/usercompany");
      },
      handleSizeChange(val) {
        console.log(`每页 ${val} 条`);
      },
      handleCurrentChange(val) {
        console.log(`当前页: ${val}`);
      },
      handleClose(done) {//这一块逻辑是点击对话框之外的区域进行提示是否关闭？
        this.$confirm('确认关闭对话框？')
          .then(_ => {
            done();
          })
          .catch(_ => {});
      }
    }
  }
</script>

<style scoped>
/* 标题部分 */
.main,header{
  width:100%;
  margin-top:-2px;
}
/* 选择栏部分 */
  nav{
    padding:15px;
  }
  /* .el-dropdown {
    vertical-align: top;
  }
  .el-dropdown + .el-dropdown {
    margin-left: 15px;
  } */
  nav div,nav input,nav button{
    margin-left:25px!important;
  }
  .el-icon-arrow-down {
    font-size: 12px;
  }
  .el-input{
    display:inline-block;
    width:300px;
  }
 footer{
    margin:10px;
    padding-right:50px;
    display:flex;   /*设置父元素是弹性布局*/
    justify-content:space-between;
    align-items:center;
 }
  .right-block,.left-block{
    display: inline-block;
  }
  .left-block{
    margin-left:35px;
  }

  /* 对话框样式 */
  .el-dialog__body{
    text-align:center!important;
  }

</style>





