        input:'',
        form: {
          type1: [],
          type2: [],
          type3: []
        }
      }
    },
    props:["people"],//接受另一个页面传来的值，后面更换成id即可
    mounted(){
      console.log(this.people);
      if(this.people){
        this.input=this.people;
      }else{
        this.title = "新增"
      }
    },
    methods:{
      handleEdit(index, rows){
        // console.log(rows[index]);
      },
      onSubmit() {
        console.log('submit!');
      }
    }
  }
</script>

<style scoped>
/* 标题部分 */
.main,header{
