    display: flex;
    justify-content: space-around;
    background-color:#FFF;
  }

  .main>section>h4{
    /* padding:10px; */
    border-bottom:1px solid #666666;
  }

  /* .el-form-item__label{
    margin:0 auto;
  } */

  .main>section>.el-button{
    margin:10px 0 10px 50px;
  }


</style>


