          $f[j].classList.add('bgc');
          // 处理不同浏览器兼容性问题
          let next = $f[j].nextElementSibling || $f[j].nextSibling;
          let prev =$f[j].previousElementSibling || $f[j].previousSibling || $f[j].parentElement;
          if(next != undefined){
            next.classList = prev.classList.remove('bgc');
          }else{
            prev.classList.remove('bgc');
          }
        }else{
          $f[j].classList.remove('bgc');
        }
