<style scoped>
/* 标题部分 */
.main,header{
  width:100%;
  margin-top:-2px;
}

/* 主体部分 */
.el-header, .el-footer {
    color: #333;
  }
  
  .el-aside {
    color: #333;
  }
  
  .el-main {
    color: #333;
    border:1px solid #BBBBBB;
  }
  
  body > .el-container {
    margin-bottom: 40px;
  }
  
  .el-container:nth-child(5) .el-aside,
  .el-container:nth-child(6) .el-aside {
    line-height: 260px;
  }
  
  .el-container:nth-child(7) .el-aside {
    line-height: 320px;
  }

  /* 项目管理--基础信息补充input */
  .basicInfo>.el-main>section>span>.el-input{
    width:120px;
    margin-right:50px;
  }

  .basicInfo>.el-footer{
    padding-bottom: 150px;
  }

  /* 按钮 */
  .el-dropdown {
    vertical-align: top;
  }
  .el-dropdown + .el-dropdown {
    margin-left: 15px;
  }
  .el-icon-arrow-down {
    font-size: 12px;
  }

  /* 输入框 */
 .el-input{
    display:inline-block;
    width:300px;
  }

  /* 时间框 */
  .el-date-editor{
    width: 217px!important;
  }

  /* 头像 */
  .head{
    border-radius:50%;
    width:100px;
    height:100px;
    cursor:pointer;
  }

  /* 个人资料 */
  .introduce{
    width:200px;
  }
  span{
    display: inline-block;
  }
  .message{
    display:flex;   /*设置父元素是弹性布局*/
    align-items:flex-start;/*垂直对齐方式*/
  }
  .el-textarea{
    width:88%;
  }
  .el-header{
    font-weight:bold;
    line-height:60px;
  }
  .el-main p,.el-main div{
    padding:6px 0;
  }

  /* 单选框 */
  .radio{
    margin-left:88px;
  }

  /* 项目图片 */
  .image span{
    margin:8px 0;
    color:#9F9F9F;
  }
  /* .message span{
    margin-left:50px;
  } */
  .el-button--text{
    border:1px solid #DCDFE6;
    width:100px;
  }
  .add{
    line-height: 129px;
  }
  .el-input-number{
    padding:0!important;
    display: inline-block;
  }
  .report{
    margin-left:100px;
  }
  .report>p>span{
    margin-right:30px;
  }

  /* 认筹信息--提交按钮 */
  .submit{
    margin-left:730px;
  }

  /* 项目标签 */
  .el-tag + .el-tag {
    margin-left: 10px;
  }
  .button-new-tag {
    margin-left: 10px;
    height: 32px;
    line-height: 30px;
    padding-top: 0;
    padding-bottom: 0;
  }
  .input-new-tag {
    width: 90px;
    margin-left: 10px;
    vertical-align: bottom;
  }

  /* 时间轴 */
  .time-shaft>div{
    display: inline-block;
  }
  .time-shaft{
    position:relative;
  }
  .time-shaft>:last-child{
    position:absolute;
    left:50px;
    top:25px;
  }

  /* hr */
  hr{
    width:80%;
    color:#BBBBBB;
    margin:10px 0;
  }

  /* 底部按钮 */
  .bottom-submit{
    margin-right:55px;
  }
  .el-footer{
    padding-top:20px;
    margin-bottom:20px;
    text-align: center;
  }
  .el-footer button{
    margin:0 50px;
  }

  /* 补充表格的表头 */
  .el-header{
    border-left:1px solid #BBBBBB;
    border-right:1px solid #BBBBBB;
  }
  .el-form-item {
    margin-bottom: 0px!important;
  }

  /* 补充认筹信息部分的样式 */
  .information>section>span{
    margin-right:200px;
  }

  .information>section>span>span:first-child,.information>footer>p>span:first-child{
    width:110px;
  }

  .information>section>span>.el-input{
    width:120px;
  }

  /* 团队成员及联系 */
  .member>.message>span:first-child{
    color:#666666;
    /* width:120px; */
    overflow:visible;
  }

  .member>.message>span:first-child>p:last-child{
    font-size:12px;
  }

  .member>.message>img,.member>.message>span:nth-child(3){
    margin:10px;
  }

  /* 常见问题 */
  .finally>section>span>.el-input:last-child{
    width:300px;
  }

  /* 表格 */
  .finally>.el-form>div>.el-form-item{
    display: inline-block;
  }

  .finally>.el-form>div>.el-form-item:first-child>.el-form-item__content>.el-input{
    width:300px;

  }

  .finally>.el-form>div>.el-form-item:first-child{
    margin:0 200px 0 10px;  
  }

  /* 做成员信息的表格 */
  .member-info{
    display: inline-block;
    width:50%;
  }

  .member-info>.el-form-item:first-child,.member-info>div:nth-child(2){
    display: inline-block;
  }

  .demo-dynamic{
    width:80%;
  }

  .el-dialog__body>span>img{
    width: 100px;
    height:100px;
    cursor:pointer;
  }

  .el-dialog__body>span>img:hover{
    /* position: fixed; */
    width: 120px;
    height:120px;
  }  

  /* 电梯 */
  .elevator{
    background-color:rgba(0,0,0,.5);
    display: inline-block;
    position:fixed;
    right:110px;
    bottom:250px;
    border-radius:5px;
    display: none;
  }

  .elevator>ul>li{
    padding:5px 10px;
    border-bottom:1px solid #fff;
    color:#fff;
  }

  .elevator>ul>li:hover{
    background-color:rgb(101, 179, 18);
    cursor:pointer;
    font-weight: bold;
  }

  .bgc{
    background-color:rgb(101, 179, 18);
  }

</style>



