<template>
  <div class="main">
    <header>
      <!-- <h4>系统设置 - 管理员管理 - {{title}}</h4> -->
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path:'/manage/setadministrator'}">项目管理</el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path:'/manage/setadministrator'}">管理员管理</el-breadcrumb-item>        
        <el-breadcrumb-item>{{title}}</el-breadcrumb-item>
      </el-breadcrumb>        
    </header>
    <nav>
      {{title}}管理员：
    </nav>
    <!-- 主体部分 -->
    <section>
      <el-form label-width="100px" :model="form" :rules="rules" ref="form" status-icon>
        <el-form-item label="账户名称：" prop="serial">
          <el-input placeholder="请输入账户名称" v-model="form.serial"></el-input>
        </el-form-item>
        <el-form-item label="账户密码：" prop="pass">
          <el-input placeholder="请输入账户密码" v-model="form.pass" type="password"></el-input> 
        </el-form-item>
        <el-form-item label="确认密码：" prop="checkPass">
          <el-input placeholder="请输入确认密码" v-model="form.checkPass" type="password"></el-input>
        </el-form-item>
        <el-form-item label="联系人名：" prop="name">
          <el-input placeholder="请输入联系人名" v-model="form.name"></el-input>
        </el-form-item>
        <el-form-item label="联系方式：" prop="phone">
          <el-input placeholder="请输入联系方式" v-model="form.phone"></el-input>
        </el-form-item>
        <el-form-item label="账户角色：" prop="people">
          <el-select v-model="form.people" placeholder="请选择账户角色" style="width:150px;">
            <el-option label="管理员" value="administrator"></el-option>
            <el-option label="市场部" value="market"></el-option>
            <el-option label="运营部" value="operate"></el-option>
            <el-option label="开发部" value="exploit"></el-option>
            <el-option label="运维部" value="operation"></el-option>
          </el-select>
        </el-form-item>        
        <el-form-item>
          <el-button type="danger" @click="onSubmit('form')" style="width:120px;">提交</el-button>
          <el-button type="info" style="width:120px;">取消</el-button>
        </el-form-item>   
      </el-form>
    </section>
  </div>
</template>

<script>
  export default {
    data() {
      var validatePass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请输入密码'));
        } else {
          if (this.form.checkPass !== '') {
            this.$refs.form.validateField('checkPass');
          }
          callback();
        }
      };
      var validatePass2 = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请再次输入密码'));
        } else if (value !== this.form.pass) {
          callback(new Error('两次输入密码不一致!'));
        } else {
          callback();
        }
      };      
      return {
        title:'修改',
        form: {
          serial:'',
          pass:'',
          checkPass:'',
          name: '',
          phone: '',
          people: ''
        },
        rules: {
          serial: [
            { required: true, message: '请输入活动名称', trigger: 'blur' },
            { min: 2, max: 10, message: '长度在 2 到 10 个字符', trigger: 'blur' }
          ],
          pass: [
            { validator: validatePass, trigger: 'blur' },
            { required: true, message: '请输入密码', trigger: 'blur' },
            { min: 8, max: 12, message: '长度在 8 到 12 个字符', trigger: 'blur' },
            { pattern: /^[A-Za-z0-9\u4e00-\u9fa5]+$/, message: '不允许输入空格等特殊符号' }
          ],
          checkPass: [
            { validator: validatePass2, trigger: 'blur' },
          ],
          name: [
            { required: true, message: '请输入联系人名', trigger: 'blur' },
            // { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }
          ],
          phone: [
            { required: true, message: '请输入联系方式', trigger: 'blur' },
            // { min: 11, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }
          ],
          people: [
            { required: true, message: '请选择账户角色', trigger: 'change' }
          ]
        },
      }
    },
    props:["serial"],//接受另一个页面传来的值，后面更换成id即可
    mounted(){
      console.log(this.serial);
      if(this.serial){
        this.form.serial=this.serial;
      }else{
        this.title = "新增"
      }
    },
    methods:{
      handleEdit(index, rows){
        // console.log(rows[index]);
      },
      onSubmit(formName) {
        // console.log('submit!');
        this.$refs[formName].validate((valid) => {
          if (valid) {
            alert('submit!');
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      }
    }
  }
</script>

<style scoped>
/* 标题部分 */
.main,header{
  width:100%;
  margin-top:-2px;
}

/* 项目名称 */
.main>nav:nth-child(2){
  font-weight: bold;
  padding:10px 20px 20px 20px;
  background-color:#fff;
}

.main>nav:nth-child(2)>span{
  color:#5167B7;
}

  /* 输入框 */
 .el-input{
    display:inline-block;
    width:150px;
  }

  /* 权限选择： */
  .main>h4{
    background-color:#FFF;
    padding: 10px 20px 20px 20px;
  }

  /* 表单部分 */
  .el-form-item-clone{
    display: inline-block;
  }

  .el-form>.el-form-item:first-child>.el-form-item__content>.el-checkbox-group>.el-checkbox,.el-form>.el-form-item:last-child>.el-form-item__content>.el-checkbox-group>.el-checkbox{
    display: block;
  }

  .el-form,.main>section>h4{
    padding:10px 50px;
    /* display: flex;
    justify-content: space-around;
    background-color:#FFF; */
  }

  .main>section{
    background: #fff;
  }

  .main>section>h4{
    /* padding:10px; */
    border-bottom:1px solid #666666;
  }

  /* .el-form-item__label{
    margin:0 auto;
  } */

  .main>section>.el-button{
    margin:10px 0 10px 50px;
  }


</style>


